

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import workflowItem from 'components/workflow/workflow-item.vue';
import {hrworkflow, hrworkflowSubmit, hrworkflowRefuse} from '@/api/workflow';
import baseLoading from 'components/base-loading.vue';
import Vue from 'vue'
import APP_CONFIGS from '@/utils/app-config'

export default {
components: {
Tab, TabItem, Popup, Grid, GridItem, workflowItem, baseLoading
},
name: 'workflow',
directives: {
TransferDom
},
data() {
return {
status: "1", //0  1 , 2
isLoadingData: true,
workflowList: [],
switchButton: true, //
filtrateShowModel: false, //
selectedOne: true,
selectedTwo: false,
selectedThree: false,
vocationType: ["", "", "", "", "", "", ""],
selected: [],
isSelect: false, // 
}
},
methods: {
onItemClick(index) {
this.isLoadingData = true;
switch (index) {
case 0:
this.status = "1";
this.getList("1");
this.switchButton = true;
break;
case 1:
this.status = "2";
this.getList("2");
this.cancel();
this.switchButton = true;
break;
case 2:
this.status = "3";
this.getList("3");
this.cancel();
this.switchButton = true;
break;
default:
break;
}
window.localStorage.setItem('workflowStatus', this.status);
},

goDetail(item){
if (this.switchButton) {//
console.log(JSON.stringify(item));
this.$router.push({
name: 'workflowDetail',
query: {
"hrWorkflowCheckStatus": item.hrWorkflowCheckStatus,
"hrWorkflowId": item.hrWorkflowId,
'hrId': item.hrId
}
});
}
},


filtrate(){
this.filtrateShowModel = true;
},

cancelModel() {
this.filtrateShowModel = false;
},
emptySelect(){
this.filtrateShowModel = false;
},

confirmModel() {
this.filtrateShowModel = false;
},
select(item, num) {
console.log(JSON.stringify(item) + num);
},



operation(){
this.switchButton = !this.switchButton;
},
selectItem(index) {
Vue.set(this.selected, index, !this.selected[index]);
if (this.selected[index]) {
for (let j = 0; j < this.workflowList.length; j++) {
if (j === index) {
this.workflowList[j].flag = true;
}
}
} else {
for (let j = 0; j < this.workflowList.length; j++) {
if (j === index) {
this.workflowList[j].flag = false;
}
}
}
console.log(JSON.stringify(this.selected));
if (this.selected.length = this.workflowList.length) {
for (let i = 0; i < this.selected.length; i++) {
if (!this.selected[i]) {
this.isSelect = false;
break;
} else {
this.isSelect = true;
}
}
}
},
selectAll(){
if (!this.isSelect) {
for (let i = 0; i < this.workflowList.length; i++) {
Vue.set(this.selected, i, true);
this.workflowList[i].flag = true;
}
this.isSelect = !this.isSelect;
} else {
for (let i = 0; i < this.workflowList.length; i++) {
Vue.set(this.selected, i, false);
this.workflowList[i].flag = false;
}
this.isSelect = !this.isSelect;
}
},

cancel(){
this.isSelect = false;
this.switchButton = true;
for (let i = 0; i < this.workflowList.length; i++) {
Vue.set(this.selected, i, false);
this.workflowList[i].flag = false;
}
},

refuseWorkflow(){
let refuseList = [];
for (let i = 0; i < this.workflowList.length; i++) {
if (this.workflowList[i].flag) {
refuseList.push({"hrWorkflowId": this.workflowList[i].hrWorkflowId});
}
}
if (refuseList.length === 0) {
this.$vux.toast.text("", 'middle');
} else {
hrworkflowRefuse({"hrWorkflowList": refuseList}).then(response => {
if (response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.text("", 'middle');
this.status = "1";
this.getList("1");
this.switchButton = true;
} else {
this.$vux.loading.hide();
}
});
}
},

passWorkflow(){
let agreeList = [];
for (let i = 0; i < this.workflowList.length; i++) {
if (this.workflowList[i].flag) {
agreeList.push({"hrWorkflowId": this.workflowList[i].hrWorkflowId});
}
}
console.log(JSON.stringify(agreeList));
if (agreeList.length === 0) {
this.$vux.toast.text("", 'middle');
} else {
hrworkflowSubmit({"hrWorkflowList": agreeList}).then(response => {
if (response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.text("", 'middle');
this.status = "1";
this.getList("1");
this.switchButton = true;
} else {
this.$vux.loading.hide();
}
});
}
},
getList(status){
hrworkflow({"page": "1", "pagesize": "10", "hrWorkflowCheckStatus": status}).then(response => {
if (response.data.success) {
this.isLoadingData = false;
this.workflowList = response.data.rows.map(function (item, index) {
item.img = require("../../assets/images/workflow/noSelect@3.png");
item.flag = false;
return item;
});
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
})
}
},
mounted(){
if (window.localStorage.getItem('workflowStatus') === '3') {
this.selectedOne = false;
this.selectedTwo = false;
this.selectedThree = true;
} else if (window.localStorage.getItem('workflowStatus') === '2') {
this.selectedOne = false;
this.selectedTwo = true;
this.selectedThree = false;
} else {
this.selectedOne = true;
this.selectedTwo = false;
this.selectedThree = false;
}
this.getList(window.localStorage.getItem('workflowStatus'));
}
}



