export default {
  components: {},
  data() {
    return {}
  },
  props: ["item", 'switchButton', 'selected'],
  mounted(){
    console.log(this.switchButton);
  },
  methods: {
    click: function () {
      this.$emit('click');
    }
  },
  computed: {
    stateImg: function () {
      return this.selected ? require('../../assets/images/workflow/allSelect@3.png') : require('../../assets/images/workflow/noSelect@3.png');
    }
  }
}



