let APP_CONFIGS = {
  getAppToken() {
    try {
      $window.ACallBackToken = function (message) {
        var obj = JSON.parse(message);
        window.localStorage.token = obj.token;
        window.localStorage.setItem('ACCESS_TOKEN', window.localStorage.token);
        window.localStorage.basePath = obj.baseUrl + "r/api?sysName=HandMobile&apiName=";
        window.localStorage.businessPath = obj.baseUrl + "r/api?sysName=HandMobile&apiName=/api/l";
        window.localStorage.queryPath = obj.baseUrl;
        window.localStorage.empno = obj.account;
        window.localStorage.userName = obj.account;
        window.localStorage.menuId = obj.menuId;
        window.localStorage.pushMess = obj.hasOwnProperty("pushextras");
      };
      $window.bCallBack = function (message) {
        hmsPopup.showShortCenterToast(message);
      };
      var dict = {
        "className": "BaseBridge",
        "function": "getBaseInfo",
        "successCallBack": "ACallBackToken",
        "failureCallBack": "bCallBack"
      };
      HandBridge.postMessage(JSON.stringify(dict));
    } catch (err) {
      //console.log(err);
    }
  }
};

export default APP_CONFIGS;





